<template>
    <div v-loading="vitualLoading"
         element-loading-text="正在加载" class="p-20">

        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入商品名称、编号"
                      @keyup.native.enter="virtualQuery"
                      v-model="goodspage.goodsName">
            </el-input>
            <span>商品类型</span>
            <el-select clearable style="width: 150px" class="filter-item" placeholder="查询所有"
                       v-model="goodspage.status">
                <el-option v-for="coinStatus in calendarTypeOptions" :key="coinStatus.key"
                           :label="coinStatus.display_name"
                           :value="coinStatus.key">
                </el-option>
            </el-select>
            <el-button class="filter-item" size="small" @click="virtualQuery">查询</el-button>
            <el-button type="primary" @click="handleDialogShow('add')" size="small" v-if="permission.add">新增</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                v-loading="listLoading"
                element-loading-text="给我一点时间"
                style="width: 100%">
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="170">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="modificationCoin(scope.row.id)" v-if="permission.edit">修改
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status===1">
                            <el-button
                                    type="text" size="small"
                                    @click="enableDisable(2,scope.row.id,'启用')" v-if="permission.enable">启用
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-else-if="scope.row.status===2">
                            <el-button
                                    type="text" size="small"
                                    @click="enableDisable(1,scope.row.id,  '禁用')" v-if="permission.disable">禁用
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="goodspage.page.pageNum"
                           :page-sizes="[10,20,30, 50]" :page-size="goodspage.page.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog title="添加虚拟币类型" :visible.sync="confirm" @close="dialogClose">

            <el-tabs type="border-card">
                <!-- ###################################### 基础信息 ################################## -->
                <el-tab-pane label="基础信息">
                    <el-form :model="GoodsVo" :rules="rules" ref="GoodsVo">
                        <el-form-item :label-width="virtualWidth" label="*商品图标" prop="goodsIconUrl">
                            <el-upload
                                    class="upload-demo"
                                    action="/upload/nofilter/file"
                                    list-type="picture"
                                    :on-success="handleSuccess"
                                    :on-remove="handleRemove"
                                    name="uploadFile"
                                    :before-upload="handleBeforeUpload"
                                    :autoUpload="true"
                                    :data="data"
                                    :file-list="fileList">
                                <el-button size="small" type="primary">上传图标</el-button>
                                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，512px*512px,且不超过2M</div>
                            </el-upload>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*商品名称" prop="goodsName">
                            <el-input class="input-short" v-model="GoodsVo.goodsName"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*商品编号" prop="goodsNo">
                            <el-input class="input-short" v-model="GoodsVo.goodsNo"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="是否允许提货">
                            <el-switch
                                    v-model="GoodsVo.isAllowDelivery"
                                    on-text=""
                                    off-text=""
                                    :on-value="2"
                                    :off-value="1">
                            </el-switch>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*每次最小提现数量" prop="perDeliveryMinNum">
                            <el-input class="input-short" v-model="GoodsVo.perDeliveryMinNum"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*每次最大提现数量" prop="perDeliveryMaxNum">
                            <el-input class="input-short" v-model="GoodsVo.perDeliveryMaxNum"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*每天最大提现数量" prop="dayDeliveryMaxNum">
                            <el-input class="input-short" v-model="GoodsVo.dayDeliveryMaxNum"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*每天最大提现次数" prop="dayDeliveryMaxTime">
                            <el-input-number class="input-short" v-model="GoodsVo.dayDeliveryMaxTime" :min="1"
                                             :max="100"
                                             auto-complete="off"></el-input-number>
                        </el-form-item>
                        <el-form-item :label-width="virtualWidth" label="*商品总量" prop="totalNum">
                            <el-input class="input-short" v-model="GoodsVo.goodsCount"
                                      auto-complete="off"></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <!--#####################################关于币种########################################-->
                <!--###############################手续费用################################-->
            </el-tabs>


            <div slot="footer" class="dialog-footer">
                <el-button @click="confirm = false">取 消</el-button>
                <el-button type="primary" @click="commitAction">确 认</el-button>
            </div>
        </el-dialog>

    </div>
</template>


<script>

    import {
        postgoodspage,
        postgoods,
        getgoods,
        putgoods,
        putgoodsstatus
    } from '@/api/virtual';

    import {
        validateChinese,
        validatePassword,
        validateEightDecimal
    } from '@/utils/validate';

    import DCTP from '@/utils/dctp';

    import {ElInputNumber} from 'element-ui/packages/input-number';

    const calendarTypeOptions = [
        {key: '', display_name: '查询所有'},
        {key: '1', display_name: '禁用'},
        {key: '2', display_name: '启用'},
    ];
    const holidayBusinessScope = [{value: 1, label: '不限制'},
        {value: 2, label: '每周六-周日'},
        {value: 3, label: '中国法定节假日（含周末）'}];

    export default {
        data() {
            let goodsIconUrl = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请上传图片'));
                }
                callback();
            };
            let goodsName = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入名字'));
                }
                callback();
            };
            let goodsNo = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入商品编号'));
                if (!validatePassword(value) || value.length > 100)
                    callback(new Error('输入字母和数字，最大长度100'));
                callback();
            };
            let validateNumber = (rule, value, callback) => {
                if (value === '')
                    callback(new Error('请输入数量'));
                if (!validateEightDecimal(value) || value * 1 >= 9999999 || value <= 0)
                    callback(new Error('手续费最大数量为9999999，小数点后最多8位'));
                callback();
            };

            return {
                listLoading: true,
                //搜索对象
                goodspage: {
                    goodsName: '',
                    status: '',
                    page: {
                        pageNum: 1,
                        pageSize: 10
                    }
                },
                //添加是的对象
                GoodsVo: {
                    goodsName: '',
                    goodsIconUrl: '',
                    goodsNo: '',
                    isAllowDelivery: '',
                    perDeliveryMinNum: '',
                    perDeliveryMaxNum: '',
                    dayDeliveryMaxNum: '',
                    dayDeliveryMaxTime: '',
                    goodsCount: ''
                },
                //下拉框属性  - 状态
                optionsStatus: [
                    {
                        value: 0,
                        label: '全部状态'
                    }, {
                        value: 1,
                        label: '启用'
                    }, {
                        value: 2,
                        label: '禁用'
                    }],
                //添加框初始隐藏
                dialogFormVisible: false,
                rules: {
                    goodsName: [
                        {validator: goodsName, trigger: 'blur'}
                    ],
                    goodsIconUrl: [
                        {validator: goodsIconUrl, trigger: 'blur'}
                    ],
                    goodsNo: [
                        {validator: goodsNo, trigger: 'blur'}
                    ],
                    perDeliveryMinNum: [
                        {validator: validateNumber, trigger: 'blur'}
                    ],
                    perDeliveryMaxNum: [
                        {validator: validateNumber, trigger: 'blur'}
                    ],
                    dayDeliveryMaxNum: [
                        {validator: validateNumber, trigger: 'blur'}
                    ],
                    dayDeliveryMaxTime: [
                        {validator: validateNumber, trigger: 'blur'}
                    ],
                    goodsCount: [
                        {validator: validateNumber, trigger: 'blur'}
                    ],
                },
                //上传参数
                data: {
                    type: 1,
                    usageType: 3
                },
                //修改
                modification: false,
                //新增
                confirm: false,
                //----------------------分割线----------------------------------
                // 搜索框
                input: '',
                vitualLoading: false,

                // 状态
                valueStatus: '',
                // 不可交易时间
                optionsHoliday: holidayBusinessScope,
                valueHoliday: '',
                valueNumber: '',
                value: '',
                virtualWidth: '160px',
                currentRow: null,
                // 表单信息
                tableData: [],
                tableHeader: [
                    {
                        label: "FID",
                        prop: "id",
                        width: 60
                    }, {
                        label: "名称",
                        prop: "goodsName",
                        width: 120
                    }, {
                        label: "商品编号",
                        prop: "goodsNo",
                        width: 120
                    }, {
                        label: "状态",
                        prop: "status",
                        width: 80,
                        formatter(row, column, value) {
                            return value === 1 ? '禁用' : (value === 2 ? '启用' : '');
                        }
                    }, {
                        label: "每次最小提现数量",
                        prop: "perDeliveryMinNum",
                        width: 160
                    }, {
                        label: "每次最大提现数量",
                        prop: "perDeliveryMaxNum",
                        width: 160
                    }, {
                        label: "每天最大提现数量",
                        prop: "dayDeliveryMaxNum",
                        width: 160
                    }, {
                        label: "每天提现次数",
                        prop: "dayDeliveryMaxTime",
                        width: 120
                    }, {
                        label: "总量",
                        prop: "goodsCount",
                        width: 160
                    }, {
                        label: "是否允许提货",
                        prop: "isAllowDelivery",
                        width: 120,
                        formatter(row, column, value) {
                            return value === 1 ? '不允许' : (value === 2 ? '允许' : '');
                        }
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        width: 180,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value);
                        }
                    }],

                calendarTypeOptions,
                //总数
                total: 0,

                formLabelWidth: '120px',
                fileList: [],
                fileListModify: [],
                action: ''
            }

        },
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        },

        //搜索功能
        methods: {
            //根据参数判断新增/修改方法
            handleDialogShow(action) {
                this.action = action;
                switch (action) {
                    case 'add':
                        this.confirm = true;
                        break;
                    case 'update':
                        this.modificationCoin();
                        break;
                }
            },
            getList() {

                this.listLoading = true;
                postgoodspage(this.goodspage).then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    this.listLoading = false;
                })
            },
            //判断添加/修改虚拟币
            commitAction() {
                switch (this.action) {
                    case 'add':
                        this.commitVirtual();
                        break;
                    case 'update':
                        this.modificationCoinBase();
                        break;
                }
            },
            dialogClose() {
                this.getList();
                this.clearData();
                this.fileList = [];
            },
            clearData() {
                this.$refs['GoodsVo'].resetFields();
            },
            checkFrom(formName) {
                this.$refs[formName].validate((valid) => {
                    return !!valid;
                });
            },
            //修改虚拟币开始
            modificationCoin(int) {
                this.action = 'update';
                getgoods(int).then(response => {
                    this.GoodsVo = response.data;
                    this.fileList = [{name: '当前图片', url: "http://image.178by.com/" + response.data.goodsIconUrl}];

                });
                this.confirm = true;
            },
            //添加虚拟币
            commitVirtual() {
                this.$refs['GoodsVo'].validate((valid) => {
                    if (valid) {
                        postgoods(this.GoodsVo).then(response => {
                            if (response.result) {
                                this.$message.success("添加成功!");
                                this.confirm = false;
                                this.getList();
                            } else {
                                this.$message.error(response.msg);
                            }
                        })
                    } else {
                        this.$message.error('请填完善内容');
                    }
                });

            },
            //修改虚拟币
            modificationCoinBase() {
                this.$refs['GoodsVo'].validate((valid) => {
                    if (valid) {
                        putgoods(this.GoodsVo).then(response => {
                            if (response.result) {
                                this.$message.success("修改成功!");
                                this.confirm = false;
                                this.getList();
                            } else {
                                this.$message.error(response.msg);
                            }
                        })
                    } else {
                        this.$message.error('请填完善内容');
                    }
                });
            }
            ,


            //搜索按钮提交数据
            virtualQuery() {
                this.getList();
            },
            //翻页的按钮
            userPage(val) {
                this.virtualcoinquery.page.pageNum = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.virtualcoinquery.page.pageSize = val;
                this.getList();
            },
            //显示添加币种页面
            increasedCoin() {
                this.confirm = true;
            },
            //-------------------------------------------分割线---------------------------------------------------------------


            //启用禁用虚拟币方法
            coinBase() {
                putgoodsstatus(this.strNum).then(response => {
                    if (response.result) {
                        this.msg = "操作成功!";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.getList();
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                });
            }
            ,
            //启用禁用虚拟币事件
            enableDisable(int, id, str) {

                this.$confirm('要' + str + '此虚拟币,是否继续', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    if (int === 1) {
                        this.strNum = '/' + id + '/1';
                        this.coinBase();
                    } else if (int === 2) {
                        this.strNum = '/' + id + '/2';
                        this.coinBase();
                    }
                }).catch(() => {
                    this.$message.info('操作已取消');
                });

            }
            ,

            //修改虚拟币结束
            //点击事件获取选中的单条信息的对象值
            /*handleCurrentChange(val) {
                this.currentRow = val;
            },*/
            handleRemove(file, fileList) {
                this.GoodsVo.goodsIconUrl = '';
            }
            ,
            handleSuccess(response, file, fileList) {
                this.fileList = [fileList[fileList.length - 1]];
                this.GoodsVo.goodsIconUrl = response.data;
            }
            ,
            handleBeforeUpload(file) {
                let filename = file.name.toLowerCase();
                if (!filename.endsWith('.jpg') && !filename.endsWith('.jpeg') && !filename.endsWith('.png')) {
                    this.$message.error('格式不对，请重新选择');
                    return false;
                }
                if (file.size > 1024 * 1024 * 2) {
                    this.$message.error('图片太大，请重新选择');
                    return false;
                }
                return true;
            }
            ,
            handleDataChange(val) {
                this.virtualCoinBase.openTime = val;
            }
        }
        ,
        created() {
            this.getList();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }

    .input-fee {
        width: 90px;
    }
</style>



